<template>
  <div id="app" @click="clickDiv">
    <router-view></router-view>
  </div>
</template>

<script>
import router from './router'
import axios from 'axios'
import experied_time from '../config/experied_time';  
import { Message } from 'element-ui'
import VueCookies from 'vue-cookies'
import {jwtDecode} from 'jwt-decode';

export default {
  name: 'App',
  is_login:false,
  ws:null,
  current_id:null,
  lastTime: null, // 最后一次点击的时间
  currentTime: null, // 当前点击的时间
  timer:1000, // 设置超时时间:10分钟
  timeInterval: '',
  // created(){
  //   // this.timeNow()
  //   // this.init()
  // },
  methods:{
    clickDiv() {
      if (this.timeInterval ==null) {
       // 1秒钟检测一次
        this.timeInterval = setInterval(this.isTimeOut, 1000);
        
      }
      this.lastTime = new Date().getTime();
      console.log(this.lastTime)
    },
  exit(id){
    axios({
      url: '/peopleApi/logout/'+id,
      method: 'put'
    }).then(res => {
      console.log(res)
      if(res.data.code ==200) {
        router.push({path:"/"}) //跳转到登录页面

       
          sessionStorage.removeItem("activeInfo")
          sessionStorage.removeItem("index")
          sessionStorage.removeItem("now")
          sessionStorage.removeItem("icon")
          sessionStorage.removeItem("token")
          sessionStorage.removeItem("image")

        Message({
          type:'error',
          message:'用户信息已过期,请重新登录'
        })
       
      }
    })
    },
    isTimeOut() {
      this.currentTime = new Date().getTime(); // 当前时间
      // 判断上次最后一次点击的时间和这次点击的时间间隔是否大于规定的时间,例:10分钟
      if (this.currentTime - this.lastTime > experied_time) {
        //判断是否为登录状态,只有登录状态下才调用方法
        if (sessionStorage.getItem("token")) {
          
          clearInterval(this.timeInterval);
          var K=jwtDecode(sessionStorage.getItem("token")).id
          this.exit(K)
          sessionStorage.removeItem('token');
          this.timeInterval = null;
        //   //先返回首页,在给提示,可以根据具体需求调整
        }
      }
    },
   }
}
  




</script>

<style>
ul {
  list-style: none;
}
a {
  text-decoration: none;
}
* {
  margin: 0;
  padding: 0;
}
#app {
  font-family: "Microsoft YaHei", "Helvetica", "Tahoma", "Geneva", "Arial", sans-serif;
  background-color: #eee;
}
</style>



