import VUE from 'vue'
import VUEX from 'vuex'

VUE.use(VUEX)

const state = {
  isPractice: false, //练习模式标志
  flag: false, //菜单栏左右滑动标志
  userInfo: null,
  menu: [
    
    {
      index: '1',
      title: '考试管理',
      icon: 'icon-kechengbiao',
      content:[
        // {item1:'功能介绍',path:'/examDescription'},
        {item1:'考试查询',path:'/selectExam',title:'考试查询',now:'0'},{item2:'添加考试',path:'/addExam',title:'添加考试',now:'1'}],
    },
    {
      index: '2',
      title: '题库管理',
      icon: 'icon-tiku',
      content:[
        // {item1:'功能介绍',path:'/answerDescription'},
        {item2:'所有题库',path:'/selectAnswer',title:'所有题库',now:'0'},{item3:'增加题库',path:'/addAnswer',title:'增加题库',now:'1'},
        {path: '/addAnswerChildren',title:'增加题库',now:'2'}],
    },
    {
      index: '3',
      title: '成绩查询',
      icon: 'icon-performance',
      content:[{item1:'学生成绩查询',path:'/allStudentsGrade',title:'学生成绩查询',now:'0'}
      ,
      {item2: '科目分段查询',path: '/selectExamToPart',title:'科目分段查询',now:'1'},
      {item3: '学生详细错题查询',path: '/selectInfo',title:'学生详细错题查询',now:'2'},
      {path: '/grade',title:'分数',now:'3'},
     
      {path: '/scorePart',title:'添加学生',now:'4'}],
    },
    {
      index: '4',
      title: '成绩发布管理',
      icon: 'icon-performance',
      content:[{item1:'统计成绩',path:'/publishScore',title:'统计成绩',now:'0'},
      {item2:'发布成绩',path:'/publishScoreNew',title:'发布成绩',now:'1'}]
      
    },
    {
      index: '5',
      title: '学生管理',
      icon: 'icon-role',
      content:[{item1:'学生管理',path:'/studentManage',title:'学生管理',now:'0'},
      {item2: '添加学生',path: '/addStudent',title:'添加学生',now:'1'}],
    },
    // {
    //   index: '5',
    //   title: '教师管理',
    //   icon: 'icon-Userselect',
    //   content:[{item1:'教师管理',path:'/teacherManage'},{item2: '添加教师',path: '/addTeacher'}],
    // },
    {
      index: '6',
      title: '评分管理',
      icon: 'icon-module4mokuai',
      content:[{item1:'主观题评分',path:'/addSubjective',title:'主观题评分',now:'0'},{item1:'平时分管理',path:'/addClassMark',title:'平时分管理',now:'1'}],
    }
  ],
}
const mutations = {
  practice(state,status) {
    state.isPractice = status
  },
  toggle(state) {
    state.flag = !state.flag
  },
  changeUserInfo(state,info) {
    state.userInfo = info
  }
}
const getters = {

}
const actions = {
  getUserInfo(context,info) {
    context.commit('changeUserInfo',info)
  },
  getPractice(context,status) {
    context.commit('practice',status)
  }
}
export default new VUEX.Store({
  state,
  mutations,
  getters,
  actions,
  // store
})



