// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import echarts from 'echarts'
import axios from 'axios'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import VueCookies from 'vue-cookies'
import store from '@/vuex/store'
import websocket from 'vue-native-websocket';
import { Message } from 'element-ui'

import WEBSOCKET_URL from '../config/websocker_url';  
import exit from '../config/exitsys_byid';
import {jwtDecode} from 'jwt-decode';
Vue.prototype.$jwtDecode=jwtDecode;
Vue.prototype.$clean=exit;
Vue.prototype.$message = Message;
Vue.prototype.$websocketUrl = WEBSOCKET_URL.WEBSOCKET_URL;
Vue.prototype.$websocketUpUrl = WEBSOCKET_URL.WEBSOCKET_UP_URL;
Vue.prototype.$websocketChatUrl=WEBSOCKET_URL.WEBSOCKET_CHAT_URL;
Vue.use(websocket, '', {
    connectManually: true, // 手动连接
    format: 'json', // json格式
    reconnection: true, // 是否自动重连
    reconnectionAttempts: 5, // 自动重连次数
    reconnectionDelay: 2000, // 重连间隔时间
});
Vue.use(ElementUI)
Vue.use(VueCookies)

Vue.config.productionTip = false
Vue.prototype.bus = new Vue()
Vue.prototype.$echarts = echarts
Vue.prototype.$axios = axios

axios.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么
  // 判断是否存在token,如果存在将每个页面header添加token

  if (sessionStorage.getItem("token")) {
    config.headers.common['token'] =sessionStorage.getItem("token");
  }
  return config
}, function (error) {
  router.push({path:'/'})
  return Promise.reject(error)
})


// 添加响应拦截器
axios.interceptors.response.use(function (response) {
  // 对响应数据做点什么
  if(response.data.code==500)
  {
    exit(jwtDecode(sessionStorage.getItem("token")).id)
  }  
  else
  return response
}, function (error) {
  // 对响应错误做点什么
  if (error.response) {
    switch (error.response.status) {
      case 401:
        console.log("我是测试，方便找一下token有没有失效")
        store.commit('del_token')
        router.push('/index')
    }
  }
  return Promise.reject(error)
})



new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App),
  components: { App },
  template: '<App/>'
})



